<template>
  <v-app>
    <v-row justify="center" align="center">
      <v-col cols="12" sm="10" md="8" lg="6">
        <v-card elevation="2" class="rounded-lg">
          <v-form ref="form" lazy-validation>
          <v-card-title class="text-h4 font-weight-bold">Register to note</v-card-title>
          <v-divider class="mx-4"></v-divider>
          <v-card-text>
            <v-row justify="center">
              <v-col cols="12" sm="6" md="6" lg="6">
                <v-text-field
                  v-model="userRegister.firstName"
                  :rules="[() => !!userRegister.firstName || 'First name is required']"
                  label="First name"
                  required
                ></v-text-field>
              </v-col>
              <v-col cols="12" sm="6" md="6" lg="6">
                <v-text-field
                  v-model="userRegister.lastName"
                  :rules="[() => !!userRegister.lastName || 'Last name is required']"
                  label="Last name"
                  required
                ></v-text-field>
              </v-col>
            </v-row>
            <v-text-field
                v-model="userRegister.username"
                :rules="[() => !!userRegister.username || 'Username is required']"
                label="Username"
                required
            ></v-text-field>
            <v-text-field
                v-model="userRegister.password"
                label="Password"
                :rules="[() => !!userRegister.password || 'Password is required', v => v.length >= 8 || 'Min 8 characters']"
                :append-icon="show ? 'mdi-eye' : 'mdi-eye-off'"
                :type="show ? 'text' : 'password'"
                name="password"
                hint="At least 8 characters"
                @click:append="show = !show"
                class="input-group--focused"
            ></v-text-field>  
            <v-text-field
                v-model="userRegister.age"
                :rules="[() => !!userRegister.age || 'Age is required', () => userRegister.age > 0]"
                label="Age"
                required
            ></v-text-field>
            <v-radio-group row v-model="userRegister.gender"
              mandatory
            >
              <label class="mr-4">Gender: </label>
              <v-radio
                label="Male"
                value="Male"
              ></v-radio>
              <v-radio
                label="Female"
                value="Female"
              ></v-radio>
              <v-radio
                label="Other"
                value="Other"
              ></v-radio>
            </v-radio-group>
          </v-card-text>
          <v-divider class="mx-4"></v-divider>
          <v-card-actions class="pa-4">
            <v-btn class="rounded-lg font-weight-bold" color="warning" @click="submit" block>Register</v-btn>
          </v-card-actions>
          <v-card-text class="font-italic text-center pt-0">Already have an account? <router-link to="/login">Login</router-link> now</v-card-text>
          </v-form>
        </v-card>
      </v-col>
    </v-row>
  </v-app>
</template>

<script>
import service from '../../service/service'
export default {
  name: 'FormSignup',
  data(){
      return{
        userRegister:{
            username: '',
            password: '',
            firstName: '',
            lastName: '',
            age:'',
            gender: '',
        },
        show: false,
      }
  },
  methods:{
      submit(e){
        e.preventDefault();
        if(this.validate()){
          service
            .register(this.userRegister)
            .then(response => {
              if(!response['isRegister']) this.$emit("snackbar", response['message'], 3)
              else{
                  this.$router.push('/home')
                  this.$emit("snackbar", response['message'], 1)
              } 
            }) 
        }
      },
      validate(){
        return this.$refs.form.validate();
      }
  } ,

}

</script>
